body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ECEFF3;
  font-family: monospace;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "TR-909";
  src: local("TR-909"),
    url(/static/media/TR-909.0556b19a.ttf) format("truetype");
  font-weight: normal;
}

.App {
    padding: 0 10px;
}

h1 {
    font-family: 'TR-909';
    font-size: 45px;
    margin-bottom: 5px;
}

h2 {
    font-family: 'TR-909';
}

a:link {
    text-decoration: none;
    color: black;
}

a:visited {
    text-decoration: none;
    color: black;
}

a:hover {
    text-decoration: none;
    color: gray;
}

a:active {
    text-decoration: none;
    color: lightgray;
}

button {
    border-radius: 0%;
    border: 1.5px solid #2B2F38;
    background-color: #C9D6DF;
    padding: 8px 5px;
    box-shadow: .2px .2px 1px #6F7C94 ;
    font-family: 'TR-909';
    font-size: 16px;
}

button:disabled {
    border: 2px solid #C9D6DF;
}

button:active {
    border: 2px solid #C9D6DF;
}

select {
    font-family: monospace;
    background-color: white;
}

option {
    font-family: monospace;
    background-color: white;
}

input {
    font-family: monospace;
}

@media all and (max-width: 373px) {
    button {
        font-size: 14px;
        padding: 7px 4px;
        
    }
  }

