.Interface {
    display: flex;
    flex-direction: column;
}
header {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

header > button {
    align-self: center;
  }

header > a {
    margin-bottom: 30px;
  }



  
.TransportControls {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.TransportSliders {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 18px;
}

input[type=range] {
    -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
    width: 225px; 
    background: #6F7C94; 
    height: 8px;
    border: 1px solid #2B2F38;
    margin: 5px;
    box-shadow: .2px .2px 1px #6F7C94 ;
  }

  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
  }

  input[type=range]::-ms-track {
    width: 100%;
    cursor: pointer;
  
    /* Hides the slider so custom styles can be added */
    background: transparent; 
    border-color: transparent;
    color: transparent;
  }

  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 1px solid #000000;
    height: 15px;
    width: 15px;
    border-radius:0px;
    background: #C9D6DF;
    cursor: pointer;
    margin-top: 0px; 
    box-shadow: .2px .2px 1px #6F7C94 ;
  }
  
  /* All the same stuff for Firefox */
  input[type=range]::-moz-range-thumb {
    border: 1px solid #000000;
    height: 15px;
    width: 15px;
    border-radius:0px;
    background: #C9D6DF;
    cursor: pointer;
    margin-top: 0px; 
    box-shadow: .2px .2px 1px #6F7C94 ;
  }
  
  /* All the same stuff for IE */
  input[type=range]::-ms-thumb {
    border: 1px solid #000000;
    height: 15px;
    width: 15px;
    border-radius:0px;
    background: #C9D6DF;
    cursor: pointer;
    margin-top: 0px; 
    box-shadow: .2px .2px 1px #6F7C94 ;
  }

.PatternSelect {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.PatternSelect > label {
    align-self: center;
    margin-right: 5px;
}

.PatternSelect_button {
    margin-left: 5px;
    margin-right: 0px;
}

@media all and (max-width: 330px) {
    .PatternSelect {
        flex-direction: column; 
        max-width: 150px;
        align-self: center;
        margin-top: 5px;
    }

    .PatternSelect > label {
        margin-bottom: 3px;
    }

    .PatternSelect_button {
        max-width: 100px;
        align-self: center;
        margin-top: 5px;
    }
  }
.Sequencer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Sequencer_display {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 20px 0px 0px 0px;
}

.Sequencer_channel {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.Sequencer_channel > label {
    margin-right: 10px;
}

@media all and (max-width: 373px) {
    .Sequencer_channel > label {
        margin-right: 5px;
        
    }
  }

.step {
    border: 1px solid black;
    border-right: none;
    margin: 0px;
    cursor: pointer;
    background-color: #FEFEFE;
    font-size: 16px;
  }
  
  .step:last-child {
     border-right: 1px solid black;
  }

  .row > .step {
    border-bottom: none;
  }
  

  .clicked {
    background-color: #6F7C94;
  }

  .current_step {
    border-left: 3px solid #2B2F38;
    
  }

  .row:last-child {
    border-bottom: 1px solid black;
  }

  @media all and (max-width: 373px) {
    .step {
        font-size: smaller;
        padding: 1.5px 0px; 
    }

    .row:last-child > .step {
      border-bottom: 1px solid black;
    }
  }


.StepTracker {
    margin-left: 52px;
}

.tracker {
    font-size: smaller;
    padding-right: 60px;
}

.second {
    margin-left: 12px;
}

.third {
    margin-left: 12px;
}

.fourth {
    margin-left: 12px;
}



@media all and (max-width: 360px) {
    .StepTracker {
        margin-left: 48px;
    }
    
    .tracker {
        font-size: smaller;
        padding-right:52px;
    }
    
    .second {
        margin-left: 10px;
    }
    
    .third {
        margin-left: 8px;
    }

    .fourth {
        margin-left: 4px;
    }
  }
.ClearSteps {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.ClearSteps > button {
